@import "mixins";

html {
    height: 100%;
}

:root {
    @include fluid-type(320px, 1200px, 12px, 16px);
}

body {
    color: #3b3b3b;
    min-height: 100%;
    word-wrap: break-word;
    font-family: 'Georgia', 'Times New Roman', 'Times', 'serif';
    line-height: 1.5;
    font-size: 87.5%;
}

a {
    text-decoration: none;
}

.link {
    font-family: 'Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';

    &:hover {
        text-decoration: none;
        border-bottom-style: solid;
    }

    &:active {
        text-decoration: none;
        border-bottom-style: solid;
    }

    &:focus {
        text-decoration: none;
        border-bottom-style: solid;
    }
}

h1,
.heading-a {
    margin: 1em 0 0.5em;
    clear: both;
    color: #000;
    font-weight: bold;
    // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
    @include fluid-type(320px, 980px, 24px, 32px);

    a {
        border-bottom: none;
    }
}

h2,
.heading-b {
    clear: both;
    margin: 1em 0 0.5em;
    font-size: 1.143em;
    font-weight: bold;
    // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
    @include fluid-type(320px, 980px, 18px, 24px);

    a {
        border-bottom: none;
    }
}

h3,
.heading-c {
    clear: both;
    margin: 1em 0 0.5em;
    font-size: 1.092em;
    font-weight: bold;
    // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
    @include fluid-type(320px, 980px, 16px, 18px);
}

h4,
.heading-d {
    clear: both;
    margin: 1em 0 0.5em;
    font-size: 1.05em;
    font-weight: bold;
    // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
    @include fluid-type(320px, 980px, 14px, 16px);
}

h5,
.heading-e {
    clear: both;
    margin: 1em 0 0.5em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-size: 0.889em;
    font-weight: bold;
    // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
    @include fluid-type(320px, 980px, 14px, 16px);
}

h6,
.heading-f {
    clear: both;
    margin: 1em 0 0.5em;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-weight: bold;
    // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
    @include fluid-type(320px, 1200px, 12px, 14px);
}

p {
    margin: 0 0 1.2em;
    // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
    @include fluid-type(320px, 1200px, 12px, 16px);
}

del {
    text-decoration: line-through;
}

blockquote {
    margin: 1.5em 10px;
    padding: 0.5em 10px;
    border-left: 1px solid #bbb;
    background: #f7f7f7;
    font-style: italic;

    &:before {
        margin-right: 0.2em;
        content: "\201C";
        vertical-align: -0.4em;
        color: #bbb;
        font-size: 3em;
        line-height: 0.1em;
    }

    &:after {
        content: "\201D";
        vertical-align: -0.45em;
        color: #bbb;
        font-size: 3em;
        line-height: 0.1em;
    }

    >p:first-child {
        display: inline;
    }
}

.node-title {
    font-weight: bold;
    text-align: center;
    @include fluid-type(320px, 980px, 24px, 30px);
}

[dir="rtl"] {
    blockquote {
        border-right: 1px solid #bbb;
        border-left: none;

        &:before {
            margin-right: 0;
            margin-left: 0.2em;
            content: "\201D";
        }

        &:after {
            content: "\201C";
        }
    }

    ul {
        padding: 0 1em 0.25em 0;

        ul {
            padding: 0 1em 0.25em 0;
        }
    }

    ol {
        padding: 0 1em 0.25em 0;

        ol {
            padding: 0 1em 0.25em 0;
        }
    }
}

.feed-icon {
    display: block;
    margin: 25px 0 0;
}

img {
    max-width: 100%;
    height: auto;

    main#content & {
        border: 1px solid #785fa0;
    }
}

main#content .align-left {
    margin-right: 1em;
}

main#content .align-right {
    margin-left: 1em;
}

ul {
    margin: 0;
    padding: 0 0 0.25em 1em;

    ul {
        margin: 0;
        padding: 0 0 0.25em 1em;
    }
}

ol {
    margin: 0;
    padding: 0 0 0.25em 1em;

    ol {
        margin: 0;
        padding: 0 0 0.25em 1em;
    }
}

dt {
    font-weight: bold;
}

dd {
    margin-bottom: 1em;
}

.fluid-columns {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    text-align: center;

    >* {
        flex: 0 1 220px;
    }
}

.align-center {
    text-align: center;
}

#navbar {
    border: unset;
    background-color: #7850b8;
    background-image: linear-gradient(to bottom, #a879f5 0%, #7850b8 100%);

    .logo {
        grid-area: logo;
        align-self: center;
    }

    a.navbar-brand {
        color: #fffeff;
        line-height: 1;
        height: auto;
        padding: 0;
        margin: 0;
        font-family: "Lucida Grande", Lucida, Verdana, sans-serif;
        font-variant: small-caps;
        font-style: italic;
        font-weight: bold;
        grid-area: site-name;
        // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
        @include fluid-type(740px, 1200px, 24px, 38px);
    }

    p.navbar-text {
        color: #fffeff;
        margin: 0;
        word-spacing: 0.1em;
        font-family: 'Georgia', 'Times New Roman', 'Times', 'serif';
        font-style: italic;
        grid-area: site-slogan;
        // fluid-type values: ($min_width, $max_width, $min_font, $max_font)
        @include fluid-type(740px, 1200px, 14px, 24px);
    }
}

.region.region-navigation {
    display: grid;
    padding: 1em 0;
    gap: 0.5rem;
    grid-template:
        "logo   site-name"
        "logo   site-slogan";
}

a,
.link {
    color: #7850b8;
}

a:hover,
a:focus,
.link:hover,
.link:focus {
    color: #803dea;
}

a:active,
.link:active {
    color: #9c64f5;
}

.region-sidebar-first {
    >[id^="block-"] {
        border-color: #785fa0;
        background-color: #efefef;
        margin: 0 0 20px;
        padding: 15px 20px;
        border-width: 1px;
        border-style: solid;

        .menu,
        .block-content {
            font-size: 1rem;
            line-height: 1.4;
        }
    }

    h2 {
        margin: 0 0 0.5em;
        padding-bottom: 5px;
        border-bottom: 1px solid #d6d6d6;
        text-shadow: 0 1px 0 #fff;
        font-size: 1.071em;
        line-height: 1.2;
    }

    tbody {
        border: none;
    }

    tr {
        &.even {
            border-bottom: 1px solid #d6d6d6;
            background: none;
        }

        &.odd {
            border-bottom: 1px solid #d6d6d6;
            background: none;
        }
    }
}

ul.menu {
    margin: 0;
    padding: 0 0 0.25em 1em;

    >li>a {
        padding: 0;
    }
}

footer.footer {
    background: #a879f5;
    color: rgba(255, 255, 255, 0.65);
}

#block-footermessage {
    font-family: "Lucida Grande", Lucida, Verdana, sans-serif;

    a {
        color: rgba(255, 255, 255, 0.8);

        &:hover {
            color: rgba(255, 255, 255, 0.95);
            text-decoration: none;
        }
    }
}